import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import wx from 'weixin-js-sdk';
import houseSelect from '@/components/common-components/houseSelect';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        // 
        this.model = { phone: '', openid: '' };
        this.fields = [{ type: 'input', modelKey: 'phone', label: '', props: { placeholder: '' }, rules: { required: true, type: 'tel' } }, { type: 'input', modelKey: 'verificationCode', label: '', props: { placeholder: '', type: 'text' }, rules: { required: true } }];
        this.valid = undefined;
        this.validity = {};
        this.options = {};
        this.time = 60; // 
        this.sendMsgDisabled = false;
        this.isVerification = false; // 
        this.openid = '';
        this.jump = ''; // 
        this.isShowMore = true;
        this.actions = [
            {
                text: ' 00.00',
                action: 'moreMoney'
            },
            {
                type: 'button',
                text: '<div class="payButton"></div>',
                action: 'payButtonClick'
            }
        ];
        this.selectCost = 0; //   
        this.totalCost = 0; //   
        this.checkList = [];
        this.billList = [];
        this.isMounted = false;
        this.houseId = '';
        // tab
        this.selectedLabelDefault = 'incomplete';
        this.tabBarList = [{
                label: '',
                value: 'incomplete'
            }, {
                label: '',
                value: 'complete'
            }];
        // 
        this.page = 1;
    }
    activated() {
        // this.pageInit()
    }
    // 
    get computedMsg() {
        return 'computed ' + this.propB;
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log(result);
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    getBindingInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '...'
            });
            toast.show();
            this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } });
                    }
                    else if (response.data.sessionId) {
                        this.$store.commit('SET_SESSION_ID', response.data.sessionId);
                        this.$store.commit('SET_USER_INFO', response.data.userInfo);
                        this.getWeiXinConfig();
                        this.getBillListByHouseId();
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('');
        }
    }
    pageInit() {
        let query = this.$route.query;
        let state = query.state;
        let houseId = '';
        if (state) {
            let stateArr = state.split('@');
            if (stateArr[0] === 'houseId') {
                houseId = stateArr[1];
            }
        }
        if (query.houseId) {
            houseId = query.houseId;
        }
        if (houseId) {
            this.houseId = houseId;
        }
        else {
            this['$createDialog']({
                type: 'alert',
                content: '',
                icon: 'cubeic-wrong'
            }).show();
            return;
        }
        if (this.$store.state.sessionId) {
            this.page = 1;
            this.billList = [];
            this.getWeiXinConfig();
            this.getBillListByHouseId();
        }
        else if (query.code) {
            this.getBindingInfo();
        }
    }
    getBillListByHouseId() {
        const toast = this['$createToast']({
            time: 0,
            txt: '...'
        });
        toast.show();
        this.$store.dispatch('getBillListByHouseId', { houseId: this.houseId, type: this.selectedLabelDefault, page: this.page, limit: 10 }).then((response) => {
            if (response.success) {
                if (this.selectedLabelDefault === 'incomplete') {
                    this.billList = response.data.list;
                    this.initCheckbox();
                }
                else {
                    if (response.data.list.length > 0) {
                        this.billList = this.billList.concat(response.data.list);
                    }
                    else {
                        this.isShowMore = false;
                    }
                }
            }
            toast.hide();
        });
    }
    initCheckbox() {
        let arr = [];
        let totalCost = 0;
        this.billList.forEach((item) => {
            arr.push(item.id);
            totalCost += item.cost;
        });
        this.totalCost = totalCost;
        this.selectCost = totalCost;
        this.actions[0].text = this.fenToYuan(totalCost);
        this.checkList = arr;
    }
    computedPrice(list) {
        let totalCost = 0;
        this.billList.forEach((item) => {
            list.forEach((id) => {
                if (item.id === id) {
                    totalCost += item.cost;
                }
            });
        });
        this.selectCost = totalCost;
        this.actions[0].text = this.fenToYuan(totalCost);
    }
    checkboxChange(list) {
        this.computedPrice(list);
    }
    clickHandler(item) {
        if (item.action) {
            this[item.action](item);
        }
    }
    fenToYuan(num) {
        if (typeof num !== 'number' || isNaN(num))
            return null;
        return ' ' + (num / 100).toFixed(2);
    }
    getWeiXinConfig() {
        // let url = window.location.origin + window.location.pathname
        let url = window.location.href;
        this.$store.dispatch('getWeixinConfig', { url: url }).then((res) => {
            console.log('', res);
            if (res.success) {
                let config = res.data;
                wx.config({
                    debug: false,
                    appId: config.appId,
                    timestamp: config.timestamp,
                    nonceStr: config.nonceStr,
                    signature: config.signature,
                    jsApiList: ['chooseWXPay'] // JS
                });
            }
            wx.ready(function () {
                console.log('ready========');
            });
            wx.error(function (err) {
                console.log(err);
                console.log('errrrrrrrrrrrrrr');
            });
        });
    }
    payButtonClick() {
        let that = this;
        if (this.checkList.length === 0) {
            that['$createDialog']({
                type: 'alert',
                content: '',
                icon: 'cubeic-important'
            }).show();
        }
        let query = {};
        query.rqBillList = this.checkList;
        query.fee = this.selectCost;
        query.body = '';
        this.$store.dispatch('setUppropertyOrder', query).then((response) => {
            if (response.success) {
                let payData = response.data;
                wx.chooseWXPay({
                    timestamp: payData.timeStamp,
                    nonceStr: payData.nonceStr,
                    package: payData.package,
                    signType: payData.signType,
                    paySign: payData.paySign,
                    success: function (res) {
                        // 
                        const toast = that.$createToast({
                            time: 2000,
                            txt: '...',
                            mask: true,
                            onTimeout: () => {
                                that.getBillListByHouseId();
                                that['$createDialog']({
                                    type: 'alert',
                                    content: '',
                                    icon: 'cubeic-ok'
                                }).show();
                            }
                        });
                        toast.show();
                    },
                    error: function () {
                    }
                });
            }
        });
    }
    tabBarChange(value) {
        this.page = 1;
        this.isShowMore = true;
        this.billList = [];
        this.getBillListByHouseId();
    }
    showMoreClick() {
        this.page++;
        this.getBillListByHouseId();
    }
    setHouseId(id) {
        if (this.houseId !== id) {
            this.houseId = id;
            this.getWeiXinConfig();
            this.getBillListByHouseId();
        }
    }
};
__decorate([
    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {
            houseSelect
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '' + (new Date(date).getMonth() + 1) + '' + new Date(date).getDate();
            },
            fenToYuan(num) {
                if (typeof num !== 'number' || isNaN(num))
                    return null;
                return '' + (num / 100).toFixed(2);
            },
            billType(type) {
                if (type === 'property') {
                    return '';
                }
                else if (type === 'parking') {
                    return '';
                }
                else if (type === 'water') {
                    return '';
                }
                else {
                    return '';
                }
            }
        }
    })
], Home);
export default Home;
